<!-- 用户菜单 -->

<template>
    <el-row class="tac">
        <el-col :span="4">
            <el-menu default-active="1" class="el-menu-vertical-demo">
                <el-menu-item index="1" @click="goVideo()">
                    <i class="el-icon-menu"></i>
                    <span slot="title">视频</span>
                </el-menu-item>
                <el-menu-item index="2" @click="goBlog()">
                    <i class="el-icon-setting"></i>
                    <span slot="title">博客</span>
                </el-menu-item>
                <el-menu-item index="3" @click="goBlogForm()">
                    <i class="el-icon-setting"></i>
                    <span slot="title">写博客</span>
                </el-menu-item>
                <el-menu-item index="4" @click="goSpace()">
                    <i class="el-icon-setting"></i>
                    <span slot="title">学员中心</span>
                </el-menu-item>
            </el-menu>
        </el-col>
        <el-col :span="20">
            <router-view />
        </el-col>
    </el-row>
</template>

<script>
const options = {
    data() {
        return{}
    },
    methods: {
        push(url) {
            this.$router.push(url);
        },
        goVideo(){
            this.push('/VideoView')
        },
        goBlog(){
            this.push('/BlogView')
        },
        goSpace(){
            this.push('/Space')
        },
        goBlogForm(){
            this.push('/BlogForm')
        }

    }
}
export default options;

</script>